.container-column,.background,.footer .footer-begin,.footer,.medium-box{display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.container-row,.footer .rss-edited,.footer .toper{display:flex;justify-content:center;align-items:center}.title-header{display:flex}.title-header .title-section{text-decoration:none;border-top:.2em solid #fff;border-bottom:.2em solid #fff;padding:.3em 1em;margin-left:1em;font-size:1.75rem;text-transform:uppercase}.medium-box{width:100%}.text{max-width:100%;max-width:45em;font-size:1.25rem;text-indent:2em;text-align:justify;margin:.5em}.text-small{font-size:1rem}.title-footer{font-size:10rem;text-transform:uppercase;color:rgba(255,255,255,.204);overflow:hidden;background-image:linear-gradient(128deg, #60aef8, #868686 50%, #60aef8);-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}.footer{width:100%;background-color:#000;position:relative;min-height:20em}.footer *+*{margin-top:.5em}.footer .footer-begin{width:100%;background-color:#fff;min-height:20em}.footer .footer-begin *{color:#000}.footer .footer-begin .toper{color:#000}.footer .toper{position:relative}.footer .toper::after{opacity:0;position:absolute;font-weight:bolder;right:0;content:"Go üîù";transition:all .25s ease;margin-top:.4em}.footer .toper:hover::after{opacity:1;right:-3.5em}.footer .rss-edited{max-width:20em}.footer .rss-edited figure picture{background-color:#000}.footer .rss-edited figure picture img{transition:all .25s ease;filter:contrast(0) brightness(2)}.footer .rss-text{font-weight:bold;text-align:center}.background{position:absolute;margin:0;width:100%;min-height:20em;background-color:#000}.background *{color:#fff}.background .rss-edited figure picture{background-color:#fff}.background .rss-edited figure picture img{transition:all .25s ease;filter:contrast(0) brightness(0)}